"use client";

// React and Next
import { useState, useEffect, useRef } from "react";
import Link from "next/link";

// API
import { CustomerLoginApi } from "@/api/clientsideaction/buyerapi";

// Utils
import { EmailValidator } from "@/utilities/regex";

// 3rd Party
// Redux
import { useDispatch } from "react-redux";
import { isLoggedinRedux } from "@/redux/slices/userSlice";

// Components
import ConnectPlugin from "@/Components/connectPlugins";
import { useTranslation } from "react-i18next";

export default function LoginForm({ addonList }) {
  const dispatch = useDispatch();
  const { t } = useTranslation();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [emailErrorShow, setEmailErrorShow] = useState(false);
  const [passwordErrorShow, setPasswordErrorShow] = useState(false);
  const [mailError, setMailError] = useState("");
  const [passError, setPassError] = useState("");
  const [passwordshow, setPasswordShow] = useState(false);
  const [enable, setEnable] = useState(false);
  const [loader, setLoader] = useState(false);
  const [emailEdit, setEmailEdit] = useState(false);
  const [passwordEdit, setPasswordEdit] = useState(false);
  const initialref = useRef(null);

  useEffect(() => {
    initialref.current.focus();
  }, []);

  useEffect(() => {
    setEmailEdit(false);
  }, [email]);

  useEffect(() => {
    setPasswordEdit(false);
  }, [password]);

  useEffect(() => {
    if (validate()) {
      setEnable(true);
    } else {
      setEnable(false);
    }
  }, [email, password, emailEdit, passwordEdit]);

  const Onkeydownfunc = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      handleSignin();
    }
  };

  const handleSignin = async () => {
    if (loader) return;

    if (validate()) {
      setLoader(true);

      const obj = {
        emailId: email,
        password: password,
        type: "normal",
        token: "",
      };
      const profiledata = await CustomerLoginApi(obj);
      if (profiledata?.status == 1) {
        localStorage.setItem("cartItems", JSON.stringify([]));
        localStorage.setItem("email", email);
        localStorage.setItem(
          "userProfile",
          JSON.stringify(profiledata.data?.user)
        );
        localStorage.setItem("password", JSON.stringify(password));
        localStorage.setItem("compareId", JSON.stringify([]));
        dispatch(isLoggedinRedux(true));
      } else {
        if (profiledata?.message?.includes("Password")) {
          setPassError(t("validationMessage.worngPassword"));
        } else if (profiledata?.message?.includes("Registered")) {
          setMailError(t("validationMessage.notRegisterEmail"));
        }
      }
      setLoader(false);
    }
  };

  const validate = () => {
    let ValidateObj = {
      email: false,
      password: false,
    };
    if (emailEdit == false) {
      if (email == "") {
        setMailError(t("validationMessage.emailEmpty"));
        ValidateObj.email = false;
      } else {
        if (EmailValidator(email)) {
          setMailError("");
          ValidateObj.email = true;
        } else {
          setMailError(t("validationMessage.incorrectEmail"));
          ValidateObj.email = false;
        }
      }
    }
    if (passwordEdit == false) {
      if (password == "") {
        setPassError(t("validationMessage.passwordEmpty"));
        ValidateObj.password = false;
      } else {
        setPassError("");
        ValidateObj.password = true;
      }
    }
    if (ValidateObj.email == true && ValidateObj.password == true) {
      return true;
    } else {
      return false;
    }
  };

  return (
    <div className="bg-blue-100 p-[40px] max-sm:p-[40px_16px] xl:px-32 md-py-20 lg:py-10 w-full md:w-1/2">
      <div className="flex justify-end items-center space-x-4 rtl:space-x-[unset] mb-12">
        <p className="text-xs font-medium leading-4 rtl:ml-4 text-black-300">
          {t("signin.DontAcc")}{" "}
        </p>

        <Link
          prefetch={false}
          href="/buyer-signup"
          className="bg-black hover:bg-zinc-800 px-6 py-2 rounded h-[33px] font-bold text-white text-base leading-4 btn"
        >
          {t("signin.Register")}
        </Link>
      </div>

      <div className="mb-10">
        <h4 className="mb-1 text-2xl font-black text-black-300">
          {t("signin.BSign")}
        </h4>

        <p className="text-base font-normal text-black-300">
          {t("signin.LogAcc")}
        </p>
      </div>

      <div className="flex flex-col mb-8 space-y-6">
        <div className="relative w-full mb-3" data-twe-input-wrapper-init>
          <input
            type="text"
            value={email}
            ref={initialref}
            autoComplete="none"
            onChange={(e) => {
              setEmail(e.target.value.trim());
            }}
            onKeyDown={(e) => Onkeydownfunc(e)}
            onBlur={() => setEmailErrorShow(true)}
            onFocus={() => setEmailErrorShow(false)}
            className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400  ${
              email !== "" &&
              emailErrorShow &&
              mailError !== "" &&
              "border-valid"
            }`}
            id="exampleFormControlInput1"
            placeholder={t("signin.Examplelabel")}
          />

          <label
            htmlFor="exampleFormControlInput1"
            className={
              email == ""
                ? `pointer-events-none bg-blue-100 absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary  `
                : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                    email !== "" &&
                    emailErrorShow &&
                    mailError !== "" &&
                    "text-red-invalid"
                  }`
            }
          >
            {t("signin.EnterEmail")}
          </label>

          {emailErrorShow && mailError && email !== "" && (
            <p className="mt-1 text-xs font-normal text-red-invalid">
              {mailError}
            </p>
          )}
        </div>

        <div className="relative w-full mb-3" data-twe-input-wrapper-init>
          <form autoComplete="off" onSubmit={(e) => e && e.preventDefault()}>
            <input
              type={passwordshow ? "text" : "password"}
              value={password}
              autoComplete="off"
              onChange={(e) => setPassword(e.target.value.trimStart())}
              onKeyDown={(e) => Onkeydownfunc(e)}
              onBlur={() => setPasswordErrorShow(true)}
              onFocus={(e) => setPasswordErrorShow(false)}
              className={`pe-[50px] peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400 mb-2.5 ${
                password !== "" &&
                passwordErrorShow &&
                passError !== "" &&
                "border-valid"
              }`}
              id="exampleFormControlInput2"
              placeholder={t("signin.Examplelabel")}
            />

            <label
              htmlFor="exampleFormControlInput2"
              className={
                password == ""
                  ? `bg-blue-100 pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary `
                  : ` bg-blue-100 translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary  text-red-valid ${
                      password !== "" &&
                      passwordErrorShow &&
                      passError !== "" &&
                      "text-red-invalid"
                    }`
              }
            >
              {t("signin.EnterPassword")}
            </label>
          </form>

          {password !== "" && passwordErrorShow && passError !== "" && (
            <p className="mt-1 text-xs font-normal text-red-invalid">
              {passError}
            </p>
          )}

          <a
            role="button"
            className="absolute text-sm font-medium cursor-pointer top-3 right-3 text-black-300"
            onClick={() => setPasswordShow(!passwordshow)}
          >
            {passwordshow ? t("signin.Hide") : t("signin.Show")}
          </a>

          <div className="flex justify-end">
            <Link
              prefetch={false}
              href="/forgot-password"
              className="text-sm font-normal underline text-link"
            >
              {t("signin.SForgotPwd")}
            </Link>
          </div>
        </div>
      </div>

      <div>
        <button
          disabled={enable == false}
          className={`w-full text-white font-bold text-base bg-boldblack btn justify-center items-center flex bg-boldblack h-11 rounded ${
            enable == true
              ? "hover:bg-zinc-800 opacity-100 cursor-pointer"
              : "opacity-15 cursor-not-allowed hover:!bg-[#191919]"
          } `}
          onClick={handleSignin}
        >
          {loader ? <Spinner /> : t("signin.SignIn")}
        </button>
      </div>

      {addonList &&
        addonList?.["facebook"]?.status == 1 &&
        ConnectPlugin.FacebookLogin && <ConnectPlugin.FacebookLogin />}

      {addonList &&
        addonList?.["facebook"]?.status == 1 &&
        ConnectPlugin.GoogleLogin && <ConnectPlugin.GoogleLogin />}
    </div>
  );
}

const Spinner = () => (
  <svg
    aria-hidden="true"
    role="status"
    className="inline w-5 h-5 text-white me-3 animate-spin"
    viewBox="0 0 100 101"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
      fill="#E5E7EB"
    />
    <path
      d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
      fill="currentColor"
    />
  </svg>
);
